body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*---------------------------------------------------------------------------*/
/* Nav */
/*---------------------------------------------------------------------------*/

.nameDiv {
  background-color: #4aaaa5;
  color:#ffffff;
  font-size: 24px;
  font-family: 'Georgia', Times, 'Times New Roman', serif;
  padding: 20px;
  width: 200px;
  text-align: center;
}

.nav {
  margin: auto 20px auto auto;
  background-color: #ffffff;
  border-color: #cccccc;
  font-family: 'Georgia', Times, 'Times New Roman', serif;
  color: #777777;
  justify-content: flex-end;
}

.divider {
  width: 1px;
  margin: 12px 6px 12px 6px;
  background: #cccccc;
  vertical-align: middle;
}

.curNav {
  background-color: #4aaaa5;
}

.max960{
  max-width: 960px;
  margin: auto;
}

.autoMarginlr{
  margin-left: auto;
  margin-right: auto;
}
/*---------------------------------------------------------------------------*/
/* Links */
/*---------------------------------------------------------------------------*/

a {
  text-decoration: none;
}

/* unvisited link */
a:link {
  color: #777777;
}

/* visited link */
a:visited {
  color: #9cbdc7;
}

/* mouse-over link */
a:hover {
  color: hotpink;
}

/* selected link */
a:active {
  color: blue;
}
/*---------------------------------------------------------------------------*/
/* Content */
/*---------------------------------------------------------------------------*/

.content {
  max-width: 920px;
  margin: auto;
  margin-top: 20px;
  background-color: #ffffff;
  border: solid 1px #cccccc;
  padding: 20px;
  font-family: 'Arial', 'Helvetica Neue', Helvetica, sans-serif;
}

.contentImage{
  margin: 20px 30px 20px 0;
  max-width: 100%;
  max-height: auto;
}

.contentText{
  flex: 1 1 auto;
  line-height: 1.5;
}

.headers {
  color: #4aaaa5;
  font-family: 'Georgia', Times, 'Times New Roman', serif;
}

.pad-20{
  padding: 20px;
}

.pad-20-l{
  padding-left: 20px;
}

.tac {
  text-align: center;
}
/*---------------------------------------------------------------------------*/
/* Portfolio */
/*---------------------------------------------------------------------------*/

.dash-line{
  border-bottom: 1px dashed black;
  margin-bottom: 20px;
}


.tile-imgContainer{
  overflow: hidden;
  max-height: 200px;
  max-width: 300px;
  margin: auto;
}

.croppedImage{
  max-height: 100%;
  max-width: 300px;
  /*padding: 20px;*/
  object-fit: cover;
  margin: auto
}

.imageLabel{
  position: relative;
  bottom: 75px;
  background-color: #4aaaa573;
  /*margin-left: 20px;*/
  width: 300px;
  text-align: center;
  padding: 10px 0px 10px 0px;
  color: #ffffff;
  margin: auto;
}

.imgLinks{
  position: relative;
  bottom: 40px;
  text-align: center;
  color: black;
}

.imgLinks a:link {
  color: darkgray;
}

.imgLinks a:visited {
  color: darkgreen;
}

.imgLinks a:hover {
  color: hotpink;
}

.imgLinks a:active {
  color: blue;
}
/*---------------------------------------------------------------------------*/
/* Contact */
/*---------------------------------------------------------------------------*/

#sep {
  color: #777777;
  background: #cccccc;
  padding-left: 20px;
  padding-right: 20px;
}


#submitForm{
  background-color: #4aaaa5;
  color:#ffffff;
  font-family: 'Georgia', Times, 'Times New Roman', serif;
  width: 100px;
  padding: 8px;
  text-align: center;
  border: none;
}
.footer {
  flex-shrink: 0;
  padding: 20px;
  background-color: #666666;
  border-top: 8px solid #4aaaa5;
  text-align: center;
  font-family: 'Arial', 'Helvetica Neue', Helvetica, sans-serif;
  color: #ffffff;
  font-size: 12px;
}
html {
    height: 100%;
}

body {
    display: flex;
    flex-direction: column;
    height: 100vh;
    margin: 0px;
    background-image: url(/static/media/dynamic-style.e9c0bc35.png);
}

.allContent {
    flex: 1 0 auto;
}


